/*热力图*/

/*top 查看详情*/
.heatmap-dialog{
  backface-visibility: hidden; /*防止页面抖动*/
  position: absolute;
  top: 0px;
  background: rgba(0, 0, 0, 0.9);
  width: 100%;
  height: 1890px;
  /*最高层级*/
  z-index: 10; 
  display: none;
  .close {
      font-size: 32px;
      color: #fff;
      background: url(../images/close.png) no-repeat;
      height: 40px;
      width: 40px;
      text-align: center;
      line-height: 40px;
      position: absolute;
      right: 155px;
      top: 125px;
      cursor: pointer;
      z-index: 1;
  }
  .heatmap-content {
    width: 3098px;
    height: 1711px;
    position: absolute;
    left: 50%;
    top: 50%;
    margin-left: -1549px;
    margin-top: -855.5px;
    background: url(../images/main/heat-map-bg.png) no-repeat;
    .totals {
      background: url(../images/county/inNetwork-bg.png) no-repeat;
      background-size: 100% 100%;
      width: 645px;
      height: 260px;
      position: absolute;
      top: 70px;
      left: 190px;
      font-family: '时尚中黑简体';
      .title {
        width: 378px;
        padding-left: 32px;
        line-height: 92px;
        margin-left: 28px;
        font-size: 46px;
      }
      .value {
        font-size: 118px;
        color: #00ffff;
        text-shadow: 3px 3px 22px #00a9ab;
        width: 340px;
        height: 125px;
        line-height: 125px;
        margin-top: 10px;
        text-align: center;
        float: right;
        cursor: pointer;
        .num-roll .num:nth-last-child(3n):after{
          margin-right: 52px;
          font-family: '微软雅黑';
        }
      }
    }
    .legend {
      position: absolute;
      bottom: 112px;
      left: 770px;
      background: url(../images/main/legend.png) no-repeat;
      width: 803px;
      height: 148px;
      font-size: 36px;
      padding: 0 10px;
      span {
        margin-top: 88px;
        float: left;
        &:last-child{
          float:right;
        }
      }
    }
  }
  .map-wrap {
    width: 100%;
    height: 100%;
    position: relative;
    path {
      cursor: pointer;
    }
    .area-map {
      position: absolute;
      left: 710px;
      top: 40px;
      z-index: 2;
      .main-small {
        background: url(../images/main/main-small-bg.png) no-repeat;
        width: 763px;
        height: 1074px;
        position: absolute;
        pointer-events: none;
        left: 66px;
        top: -73px;
        z-index: 2;
      }
    }
    .main-map {
       transform: scale(0.52, 0.45);
       left: 564px;
       top: -341px;
       z-index: 2;
       svg {
         display: none;
       }
       .names-canvas {
         display: none;
       }
      .area-small {
        background: url(../images/main/cq-small-bg.png) no-repeat;
        width: 690px;
        height: 803px;
        position: absolute;
        pointer-events: none;
        left: -220px;
        top: -156px;
        /*默认隐藏，切换显示*/
        display: none; 
      } 
    }
    .main-btn {
      width: 470px;
      height: 590px;
      position: absolute;
      left: 938px;
      top: 69px;
      border-radius: 26px;
      cursor: pointer;
      /*层级要大于实际画出来的地图*/
      z-index: 4; 
    }
    .area-btn {
      width: 470px;
      height: 586px;
      position: absolute;
      left: 927px;
      top: 126px;
      border-radius: 26px;
      cursor: pointer;
      z-index: 4;
      display: none;
    }
    canvas {
      pointer-events: none;
    }
    .heatmap-canvas {
      left: 120px;
    }
    .area-names-svg, 
    .main-names-svg {
      position: absolute;
      left: 0;
      top: 0;
      z-index: 1;
      .name-group {
        cursor: pointer;
      }
    }
  }
}



