* {
  margin: 0;
  padding: 0;
}
html {
  height: 100%;
}
body {
  background: url(../images/body-bg.jpg) no-repeat;
  background-size: 100% 100%;
  width: 3360px;
  height: 1890px;
  color: #fff;
  font-family: '微软雅黑';
  font-size: 45px;
}
 
@font-face {
  font-family: 'digifacewide';
  src: url('../../../loader/common/fonts/digifacewide.ttf');
}

@font-face {
  font-family: '微软雅黑';
  src: url('../../../loader/common/fonts/msyh.ttf');
}

/*滚动条样式 start */
::-webkit-scrollbar {
    width: 7px;
    height: 6px;
}

::-webkit-scrollbar-track: {
    background-color: #fff;
}

::-webkit-scrollbar-thumb {
    background-color: #00f9f2;
    border-radius: 20%;
}

::-webkit-scrollbar-button {
    background-color: #00f9f2;
    display: none;
}

::-webkit-scrollbar-corner {
    background-color: black;
}
/*滚动条样式 end */

/*修改placeholder默认颜色*/
:-moz-placeholder { 
    color: #fff; opacity:1; 
    font-family: '微软雅黑';
}

::-moz-placeholder { 
    color: #fff;opacity:1;
    font-family: '微软雅黑';
}

/*placeholder默认颜色*/
input:-ms-input-placeholder{
    color: #fff;opacity:1;
    font-family: '微软雅黑';
}

input::-webkit-input-placeholder{
    color: #fff;opacity:1;
    font-family: '微软雅黑';
}

/*头部样式*/
.dynamic-header {
  height: 80px;
  position: relative;
  .top-title {
    position: absolute;
    top: 5px;
    left: 1380px;
    height: 70px;
    width: 610px;
  }
  .all-area {
    position: absolute;
    top: 30px;
    left: 120px;
    cursor: pointer;
    font-size: 45px;
    z-index: 4;
    .area-name {
      background: url(../images/area-list-bg.png) no-repeat;
      width: 372px;
      height: 100px;
      cursor: pointer;
    }
    .area-name input {
      width: 215px;
      padding-left: 5px;
      border: none;
      height: 75px;
      line-height: 75px;
      margin: 11px 0 0 27px;
      background: none;
      font-size: 52px;
      color: #fff;
      outline: none;
      cursor: pointer;
      font-family: '微软雅黑';
    }
    .area-list {
      display: none;
      list-style: none;
      border: 1px solid #00f9f2;
      max-height: 320px;
      overflow-y: auto;
      position: absolute;
      top: 100px;
      background: #0a1265;
      left: 5px;
      width: 97%;
      /*background: url(../images/county/li-bg.png) repeat-y;*/
      li {
         padding-left: 32px;
         background: url(../images/county/li-bg.png) no-repeat;
         background-size: 100% 100%;
         height: 82px;
         line-height: 82px;
        &:hover  {
          background:#151d3c;
        }
      }
    }
  }
  .header-info {
    font-size: 30px;
    position: absolute;
    right: 128px;
    top: 30px;
    .current-time {
      color: #00ffff;
      font-size: 36px;
      height: 24px;
      line-height: 24px;
      font-family: '时尚中黑简体';
      margin-bottom: 15px;
    }
    p {
      float: left;
      line-height: 36px;
      margin-top: 18px;
      color: #d5d5d5;
    }
    .user {
      padding-right: 20px;
    }
    .out:hover {
      color: #fff;
      cursor: pointer;
    }
    i {
      width: 30px;
      height: 30px;
      display: inline-block;
      color: #fff;
    }
    i.user-icon {
      background: url(../images/user-icon.png) no-repeat;
    }
    i.out-icon {
      background: url(../images/out-icon.png) no-repeat;
    }
  }
}

/*在网终通用样式*/
.in-network {
  /*标题*/
  .title {
    width: 378px;
    padding-left: 20px;
    line-height: 86px;
    margin-left: 28px;
    font-size: 46px;
  }
  /*数字*/
  .number {
    font-size: 92px;
    color: #00ffff;
    text-shadow: 3px 3px 22px #00a9ab;
    width: 340px;
    height: 120px;
    line-height: 120px;
    text-align: center;
    float: right;
    cursor: pointer;
  }
}


/*数字滚动的样式调整*/
.num {
  backface-visibility: hidden; /*防止页面抖动*/
  /*transform-style: preserve-3d;*/
}
.num-roll .num {
  pointer-events: none;
}
.num-roll .num:nth-last-child(3n):after{
  content: ',';
  color: #00ffff;
  display: block;
  margin-top: -120px;  
}
.num-roll .num:first-child:after{
  content: '';
}

/*在网终端下拉框*/
.pages-left{
  /*下拉弹框*/
  .drop-down-list {
      width: 580px;
      border:2px solid #00ffff;
      border-top: none;
      position: absolute;
      left: 20px;
      top: 247px;
      display: none;
      z-index: 3;
      .list-no-data {
        margin-top: 42px;
        font-size: 34px;
      }
      .top-border {
        background: url(../images/top-line.png) no-repeat;
        background-size: 100% 100% ;
        height: 14px;
        position: absolute;
        width: 100%;
        top: -12px;
      }
      .list-container {
        background: rgba(3, 10, 29, 0.9);
        line-height: 95px;
        padding: 10px 0 20px 0px;
        padding-left: 4%;
        width: 96%;
        float: left;
        font-size: 39px;
        p {
          display: block;
          float: right;
          text-align: right;
          width: 76%;
          b {
            font-size: 34px;
            float: left;
            padding: 0 5px;
            &:last-child {
              padding: 0;
              float: initial;
            }
          }
          span {
             color: #00ffff;
             font-family: '时尚中黑简体';
             padding: 0 10px;
             display: block;
             float: left;
             text-align: right;
              width: 120px;
            &:first-child {
              width: 150px;
            }
          }
        }
        li {
          width: 100%;
          display: block;
          float: left;
        }
      }
      .control-drop {
        padding-left: 8%;
        width: 92%;
        font-size: 45px;
        li {
          width: 91%;
        }
        p {
          width: 35%;
          text-align: right;
          display: block;
          float: right;
        }
        .control-all-btn {
          background: url(../images/all-btn.png) no-repeat;
          width: 175px;
          height: 48px;
          line-height: 48px;
          font-size: 27px;
          color: #00ffff;
          float: right;
          text-align: center;
          margin: 5% 13% 2% 0;
          cursor: pointer;
          &:hover {
            color: #ffca14;
          }
        }
      }
    }
}        


