.out-cq-top {
    position: absolute;
    top: 0;
    right: 0;
}

.top10-container {
    position: relative;
    /*top: 100px;
    left: 100px;*/
    font-family: 微软雅黑;
    .nav-group {
        height: 62px;
         .nav {
            float: left;
            width: 243px;
            height: 62px;
            margin-top: 3px;
            text-align: center;
            color: #fff;
            font-size: 33px;
            line-height: 62px;
            cursor: pointer;
            background-image: url(../images/top10/nav-bg.png);
            background-repeat: no-repeat;
            &.active {
                color: #00ffff;
                background-image: url(../images/top10/nav-active-bg.png);
            }
            &:nth-child(1) {
                margin-left: 55px;
            }
            &:not(:first-child) {
                margin-left: 5px;
            }
         }
    }
    .top10-title {
        position: absolute;
        width: 835px;
        height: 119px;
        color: #ffffff;
        font-size: 42px;
        /*text-indent: 1em;*/
        padding-left: 40px;
        line-height: 119px;
        background-image: url(../images/top10/top-title.png);
        background-repeat: no-repeat;
        z-index: 2;
        .top-details {
            float: right;
            color: #e5e5e5;
            font-size: 27px;
            margin-right: 10px;
            &:hover{
                color:#fff;
                cursor: pointer;
            }
        }

    }
    .list-container {
        position: relative;
        top: 40px;
        width: 877px;
        height: 1057px;
        background-image: url(../images/top10/list-bg.png);
        background-repeat: no-repeat;
        padding-top: 110px;
        z-index: 1;
        .list-title {
            position: relative;
            color: #00ffff;
            font-size: 46px;
            top:10px;
            font-family: '微软雅黑';
            a {
              display: inline-block;
              text-align: center;
              &:nth-child(1) {
                width: 200px;
              }
              &:nth-child(2) {
                width: 360px;
                text-align: right;
                padding-right: 10px;
              }
              &:nth-child(3) {
                width: 145px;
                font-size: 36px;
                padding-left: 90px;
                text-align: right;
              }
            }
        }
        /*地级市*/
        .top10-list.city-list li a{
          &:nth-child(1) {
            width: 352px;
          }
          &:nth-child(2) {
            width: 230px;
          }
          &:nth-child(3) {
            width: 245px;
          }
        }
        .top10-list {
            position: relative;
            top: 65px;
            color: #e5e5e5;
            font-size: 45px;
            li {
                height: 78px;
                line-height: 73px;
                a {
                    display: inline-block;
                    float: left;
                    /*pointer-events: none;*/
                    span {
                        font-size: 32px;
                        padding-left: 5px;
                    }
                    i {
                        color: #b6c0ca;
                        font-size: 74px;
                        float: left;
                        width: 37px;
                        height: 44px;
                        display: inline;
                        margin-left: 36px;
                        font-family: '微软雅黑';
                        margin-top: 18px;
                    }
                    .equal {
                        font-family: cursive;
                        margin-top: -9px;
                        margin-left: 28px;
                    }
                    .up-icon {
                        display: inline-block;
                        height: 44px;
                        background-image: url(../images/top10/up.png);
                    }
                    .down-icon {
                        display: inline-block;
                        height: 44px;
                        background-image: url(../images/top10/down.png);
                    }
                    &:nth-child(1) {
                        width: 290px;
                        text-indent: 1em;
                        text-align: left;
                        /*text-indent: 2em;*/
                        overflow: hidden;
                        text-overflow: ellipsis;
                        white-space: nowrap;
                    }
                    &:nth-child(2) {
                        width: 280px;
                        /*text-indent: 1em;*/
                        font-family: digifacewide;
                        text-align: right;
                        padding-right: 10px;
                        font-size: 40px;
                    }
                    &:nth-child(3) {
                        width: 225px;
                        text-align: right;
                        color: #fff;
                        font-family: digifacewide;
                        font-size: 30px;
                        float: right;
                        margin-right: 32px;

                    }
                }
            }
        }
    }
}
.in-cq-top .top10-list li a:first-child{
    cursor: pointer;
}
/*暂无数据*/
.list-no-data {
    font-size: 46px;
    text-align: center;
    margin-top: 260px;
}
/*top 查看详情*/
.control-detials-dialog,
.top-detials-dialog{
    backface-visibility: hidden; /*防止页面抖动*/
    position: absolute;
    top: 0px;
    background: rgba(0, 0, 0, 0.9);
    width: 100%;
    height: 1890px;
    display: none;
    z-index: 5;
    .close {
        font-size: 32px;
        color: #fff;
        background: url(../images/close.png) no-repeat;
        border-radius: 50%;
        height: 40px;
        width: 40px;
        text-align: center;
        line-height: 40px;
        position: absolute;
        right: 20px;
        top: 20px;
        cursor: pointer;
    }
    .detials-content {
        background: url(../images/deitals-bg.png) no-repeat;
        background-size: 100% 100%;
        width: 1900px;
        height: 1597px;
        position: absolute;
        left: 50%;
        top: 50%;
        margin-left: -950px;
        margin-top: -786.5px;
        .search {
            padding-top: 20px;
            float: left;
            /*width: 87%;*/
            width: 89%;
            input {
                outline: none;
                background: none;
                float: left;
                border: none;
            }
            input[type='text'] {
                width: 92%;
                height: 72px;
                font-size: 34px;
                color: #e7e7e7;
                line-height: 72px;
                font-family: '微软雅黑';
                /*padding: 0 0% 0 8%;*/
                padding: 0 0% 0 4%;
            }
            input[type='button'] {
                width: 53px;
                height: 72px;
                margin-top: 1%;
                float: right;
                cursor: pointer;
            }
        }
        .top-title {
            font-size: 60px;
            color: #ffffff;
            width: 1076px;
            height: 110px;
            line-height: 110px;
            float: left;
            margin-top: 33px;
            padding-left: 50px;
        }
        .search-container {
            float: left;
            margin-top: 35px;
            width: 100%;
            .list-container {
                float: left;
                width: 100%;
                .list-title {
                    float: left;
                    width: 97.2%;
                    padding-left: 2.8%;
                    color: #00ffff;
                    line-height: 100px;
                    font-size: 58px;
                    li {
                        float: left;
                        &:nth-child(1) {
                            width: 8%;
                            padding-left: 3%;
                        }
                        &:nth-child(2) {
                            width: 29%;
                            text-align: left;
                            
                        }
                        &:nth-child(3) {
                            width: 33%;
                            text-align: right;
                        }
                        &:nth-child(4) {
                            width: 10%;
                            font-size: 42px;
                            padding-left: 12%;
                            text-align: right;
                            line-height: 45px;
                            padding-top: 10px;
                        }
                    }

                }
                .search-list.city-list li a{
                  &:nth-child(2) {
                    width: 51%;
                  }
                  &:nth-child(3) {
                    width: 11%;
                  }
                }
                .search-list {
                    height: 1100px;
                    overflow-y: auto;
                    width: 100%;
                    margin-top: 40px;
                    float: left;
                    font-size: 50px;
                    li{
                        height: 110px;
                        line-height: 110px;
                        float: left;
                        width: 97.2%;
                        padding-left: 2.8%;
                        &:hover {
                          background: #4d321c;
                          cursor: pointer;
                        }
                        a{
                            display: block;
                            float: left;
                            height: 110px;
                           /* pointer-events: none;*/
                            &:nth-child(1) {
                                width: 8%;
                                padding-left: 3%;
                                text-align: left;
    
                            }
                            &:nth-child(2) {
                                width: 42%;
                                /*padding-right: 6%;*/
                                text-align: left;
                                overflow: hidden;
                                text-overflow: ellipsis;
                                white-space: nowrap;
                            }
                            &:nth-child(3) {
                                width: 20%;
                                font-family: digifacewide;
                                text-align: right;
                                font-size: 42px;
                            }
                            &:nth-child(4) {
                                width: 17%;
                                text-align: right;
                                padding-left: 5%;
                                font-family: digifacewide;
                                color: #e08a26;
                                font-size: 42px;
                            }
                            i {
                                color: #b6c0ca;
                                font-size: 74px;
                                float: left;
                                width: 37px;
                                height: 44px;
                                display: inline;
                                margin-left: 10%;
                                font-family: '微软雅黑';
                                margin-top: 41px;
                            }
                            .equal {
                                font-family: cursive;
                                margin-top: 5px;
                            }
                            .up-icon {
                                display: inline-block;
                                height: 44px;
                                background-image: url(../images/top10/up.png);
                            }
                            .down-icon {
                                display: inline-block;
                                height: 44px;
                                background-image: url(../images/top10/down.png);
                            }
                          }  
                    }
                }
            }
        }

        /*管控区域排行详情*/
        .search-container.control-container {
          .list-container {
              width: 100%;
              .list-title {
                li {
                  float: left;
                  &:nth-child(1) {
                    width: 24%;
                    padding-left: 5%;
                  }
                  &:nth-child(2) {
                    width: 40%;
                      
                  }
                  &:nth-child(3) {
                    width: 24%;
                    text-align: right;
                  }
                }
              }
          } 
          .list-container .search-list {
            li{
              a{
                display: block;
                float: left;
                &:nth-child(1) {
                  width: 25%;
                }
                &:nth-child(2) {
                  width: 40%;
                }
                &:nth-child(3) {
                  width: 22%;
                }
              }  
            }
          }
        }
    }
    .close {
        width: 39px;
        height: 39px;
        position: absolute;
        right: 20px;
        top: -5px;
    }
}

/*管控区域人数 查看全部*/
.control-detials-dialog {
    position: absolute;
    top: 0px;
    background: rgba(0, 0, 0, 0.9);
    width: 100%;
    height: 1890px;
    display: none;
    z-index: 4;
    .detials-content {
        background: url(../images/contral-deitals-bg.png) no-repeat;
        background-size: 100% 100%;
        width: 1090px;
        height: 1597px;
        position: absolute;
        left: 50%;
        top: 50%;
        margin-left: -545px;
        margin-top: -786.5px;
      .top-title {
        margin-top: 45px;
      }
      .search {
        width: 81%;
        input[type='text'] {
          padding: 0 0% 0 8%;
          width: 85%;
        }
      }
    }

}



