/*主城样式表*/

.dynamic-main-templates {
   position: relative;
   top: 90px;
   left: 135px;
   height: 1660px;
   width: 3080px;
   font-size: 45px;
/*   display: none;*/
  .value {
    color: #00fefe;
  }
  /*左边*/
  .pages-left {
    float: left;
    width: 1255px;
    .in-network {
      .terminal,
      .peopleNumer {
        float: left;
        background: url(../images/inNetwork-bg.png) no-repeat;
        width: 630px;
        height: 231px;
        margin-right: 15px;
      }
      .peopleNumer {
        margin: 0;
      }
    }
      .phone-number,
      .in-network {
        float: left;
        width: 103%;
      }
      /*在渝、出渝手机数量统计*/
      .phone-number {
        .count {
          width: 100%;
          float: left;
        }
        /*在渝手机数量统计*/
        .in-count {
          margin-top: 100px;
          .count-list {
            background: url(../images/in-yu-count-bg.png) no-repeat;
            width: 1280px;
            height: 233px;
            li {
              width: 380px;
              height: 210px;
              margin-left: 20px;
              color: #fff;
              p {
                padding-right: 10px;
              }
            }
            .value {
              margin-top: -10px;
              margin-right: -8px;
              width: 150px;
              float: right;
      
              font-size: 48px; /*设计图大小为65*/
            }
            .abroad {
              .value {
                padding-left: 140px;
              }
            }
            /*.local {
              .value {
                margin-right: -34px;
              }
            }*/
          }
        }
        /*出渝手机数量统计*/
        .out-count {
          margin-top: 90px;
          .count-list {
            background: url(../images/out-yu-count-bg.png) no-repeat;
            width: 1256px;
            height: 257px;
            margin-bottom: 35px;
            li {
              width: 580px;
              height: 255px;
              padding-right: 30px;
              margin-left: 25px;
              &:first-child {
                margin: 0;
              }
            }
            .value {
              font-size: 80px;
              margin-top: 23px;
      
            }
          }
        }
        .title {
          background: url(../images/title-bg.png) no-repeat;
          width: 1254px;
          height: 110px;
          padding-left: 28px;
          margin-bottom: 20px;
          font-size: 46px;
          line-height: 110px;
        }
      }
  }
  .pages-right {
    position: absolute;
    right: 0px;
    width: 1770px;
    height: 1235px;
  }
  /*.pages-left,
  .pages-bottom {
    .title {
      font-size: 20px;
      color: #fff;
      line-height: 57px;
    }
  }*/
  .pages-bottom {
    position: absolute;
    bottom: 40px;
    background: url(../images/controlTaskCout-bg.png) no-repeat;
    width: 3066px;
    height: 355px;
    background-size: 100% 100%;
    float: left;
    .title {
      line-height: 100px;
      margin-left: 75px;
      width: 286px;
      height: 110px;
      font-size: 46px;
      span {
        font-size: 38px;
        color: #00ffff;
        margin-left: 30px;
        padding-top: -8px;
        float: right;
        display: inline-block;
      }
    }
    .count-list {
      li {
        background: url(../images/count-bg.png) no-repeat;
        width: 301px;
        height: 167px;
        margin: 35px 125px 0 45px;
        font-size: 38px;
        padding-top: 10px;
        p:first-child {
          width: 300px;
          text-align: center;
          margin-left: 15px;
        }
        &:first-child {
          margin-left: 100px;
        }
      }
      .value {
        font-size: 70px;
        margin-top: 27px;
        padding-right: 30px;

      }
      .warning-number .value {
        float: right;
        margin-right: -40px;
        margin-top: 5px;
      }
      div.value {
        font-size: 70px;
        margin-top: 0px;
        margin-right: 20px;
      }
    }
  } 
  .count-list {
    li {
      float: left;
      text-align: right;
      padding: 45px 25px 0 0;
    }
  }
  .number,
  .value {
    font-family: '时尚中黑简体';
  }
}

/*显示所有地区名字*/
.show-all-name {
  position: absolute;
  left: 0;
  top: 0;
  background: rgba(5, 45, 104, 0.8);
  padding: 10px;
  border-radius: 2px;
  z-index: 10;
  display: none;
  font-size: 45px;
}
 

/*全市在网终端、 管控区域人数*/
.terminal,
.peopleNumer{
  .num-roll .num:nth-last-child(3n):after{
    /*font-size: 96px;*/
    margin-right: 52px;
    font-family: '微软雅黑';
  }
}

/*预警次数*/
.warning-number {
  .num-roll .num:nth-last-child(3n):after {
    margin-right: 20px;
  }
}

/*在渝手机数量统计*/
.in-count {
  .num-roll .num:nth-last-child(3n):after {
     margin-right: 26px;
  }
}



