/*区县样式表*/
.dynamic-county-templates {
   position: relative;
   top: 90px;
   left: 135px;
   height: 1660px;
   width: 3080px;
   font-size: 45px;
   display: none;
   .pages-left {
      position: absolute;
      left: 0;
      top: 60px;
      width: 725px;
      height: 1225px;
      .drop-down-list {
        width: 627px;
        top: 263px;
      }
      .in-network {
        background: url(../images/county/inNetwork-bg.png) no-repeat;
        width: 638px;
        height: 247px;
        float: left;
        padding-right: 25px;
        .number {
          font-size: 118px;
          margin-top: 12px;
          div{
             pointer-events: none;
          }
        }
      }
      /*手机数量统计*/
      .phone-number {
        width: 530px;
        height: 795px;
        float: left;
        margin-top: 145px;
        .title {
          background: url(../images/county/title-bg.png) no-repeat;
          width: 515px;
          height: 101px;
          line-height: 95px;
          padding-left: 20px;
        }
        .count-list {
          float: left;
          li {
            float: left;
            background: url(../images/county/phone-count.png) no-repeat;
            width: 490px;
            height: 159px;
            margin-top: 40px;
            text-align: right;
            padding: 20px 22px 0 0;
          }
          .value {
            float: right;
            color: #00fefe;
            font-size: 70px;
            margin: -10px -20px 0 0;
            height: 120px;
          }
        }
      }
   }
   /*中间地图和运营商*/
   .pages-center {
      width: 1800px;
      height: 1225px;
      left: 530px;
      position: absolute;
      /*.map-main {
        position: absolute;
        z-index: 0;
        display: none;
      }*/
      .area-operator {
        width: 495px;
        height: 675px;
        position: absolute;
        left: 500px;
        top: 130px;
        pointer-events: none;
        z-index: 1;
        .operator-top-title {
          /*display: none;*/
        }
        canvas.light-canvas {
          position: absolute;
          left: 222px;
          top: 400px;
        }
        svg {
          position: absolute;
          left: 0;
          top: 0;
          width: 690px;
          height: 850px;
          padding-top: 5px;
 
        }
        .title {
          font-size: 50px;
          width: 400px;
          float: left;
          margin-top: 26px;
          p {
            font-size: 30px;
            color: #cbcbcb;
            text-align: right;
            line-height: 50px;
          }
        }
        .operator-list {
          float: left;
          li {
            background: url(../images/county/operator-bg.png) no-repeat;
            background-size: 100% 100%;
            width: 396px;
            height: 150px;
            margin-bottom: 15px;
            list-style: none;
            .row-1 {
              float: left;
              height:  120px;
              &:nth-child(1) {
                width: 60%;
                padding: 3% 0 0 10%;
              }
              &:nth-child(2) {
                width: 30%;
                float: right;
                color: #b4c7f9;
                margin-top: 13%;
                font-size: 34px;
                line-height: 40px;
                span:last-child {
                  color: #44ff86;
                  font-family: '时尚中黑简体';
                  em {
                    font-size: 30px;
                  }
                }
              }
            }
            .item-title {
              font-size: 32px;
            }
            .item-value {
              font-size: 50px;
              margin-top: 26px;
              height: 80px;
              margin-right: 18px;
              overflow: hidden;
              .value {
                float: right;
              }
            }
          }
        }
        /*线条*/
        .cls-1 {
          stroke: #00ffff;
          stroke-linecap: round;
          stroke-width: 2px;
          stroke-dasharray: 25, 10;
          fill: none;
          fill-rule: evenodd;
        }
        .light {
          background: url(../images/county/light.png) no-repeat;
          width: 474px;
          height: 473px;
          position: absolute;
          bottom: -208px;
          right: -219px;
          animation:scale 3s infinite;
        }
        .light.animation1 {
          transition: all 0.4s;
          transform: scale(1.5);
          opacity: 0.2;
        }
        .light.animation2 {
          transition: all 0.6s;
          transform: scale(0.8);
          opacity: 1;
        }
        .center-name {
          background: #0ffffe;
          font-size: 40px;
          color: #1c2320;
          padding: 15px 20px;
          box-shadow: 5px 5px 12px #0a1a34;
          position: absolute;
          right: -90px;
          bottom: 0;
          border-radius: 8px;
          z-index: 2;
        }
      }
   }
   .pages-right {
    width: 887px;
    position: absolute;
    right: 0;
    top: 50px;
    height: 1235px;
    .top10-container {
      /*导航*/
      .nav-group {
        .nav {
          background: url(../images/county/nav-bg.png) no-repeat;
          width: 243px;
          height: 62px;
        }
        .nav.active {
          background: url(../images/county/nav-bg2.png) no-repeat;
        }
      }
      /*标题*/
      .top10-title {
        background: none;
      }
      /*内容*/
      .list-container {
        background: url(../images/county/list-bg.png) no-repeat;
        width: 878px;
        height: 1188px;
        top: 0;
        padding-top: 136px;
      }
    }
   }
  .number,
  .value {
    font-family: '时尚中黑简体';
    color: #00ffff;
    text-shadow: 3px 3px 15px #00a9ab;
  }
   /*手机数量统计*/
  .phone-number {
    .num-roll .num:nth-last-child(3n):after {
      margin-right: 30px;
    }
  }
 }


/*运营商*/
.operator-list .num-roll .num:nth-last-child(3n):after{
  margin-top: -56px;
  margin-right: 18px;
  /*font-size: 46px;*/
  font-family: '微软雅黑';
  float: right;
}


.operator-list {
  .num-roll .num{
    margin:0 2px;
  }
}

@keyframes scale {
0% {
  transform: scale(1);
  opacity: 1
}
50% {
  transform: scale(0.7);
  opacity: 0.6
}
100% {
  transform: scale(1);
  opacity: 1
}
}




